import { initializeApp } from "firebase/app";
import { getDatabase } from 'firebase/database';
import { getFirestore, collection, addDoc, setDoc, doc, getDoc,getDocs,query, where ,updateDoc } from 'firebase/firestore';
import { getStorage, ref, uploadBytes, getDownloadURL } from 'firebase/storage';

class FireBaseConexion {
    constructor() {
        const firebaseConfig = {
            apiKey: "AIzaSyD_hSp-vn7ydzLZ31KdgeWWiJTRT7ySlgU",
            authDomain: "joaquinburgosportfolio.firebaseapp.com",
            databaseURL: "https://joaquinburgosportfolio-default-rtdb.firebaseio.com",
            projectId: "joaquinburgosportfolio",
            storageBucket: "joaquinburgosportfolio.appspot.com",
            messagingSenderId: "707181594985",
            appId: "1:707181594985:web:e806bfd1df2603631215f8",
            measurementId: "G-P77VCRCR3B"
        };

        const app = initializeApp(firebaseConfig);
        this.firestore = getFirestore(app);
        this.db = getDatabase(app);
        this.storage = getStorage(app); // Guardar la instancia de Storage en la propiedad de la clase
    }

    // Add or update a document in Firestore
    async setOrUpdateFireStore(collectionName, json, docId = null) {
        try {
            if (docId) {
                // Update specific document
                const docRef = doc(this.firestore, collectionName, docId);
                await setDoc(docRef, json, { merge: true });
                console.log("Document updated with ID: ", docId);
            } else {
                // Add new document if no docId is provided
                const collectionRef = collection(this.firestore, collectionName);
                const docRef = await addDoc(collectionRef, json);
                console.log("Document added with ID: ", docRef.id);
            }
        } catch (e) {
            console.error("Error adding or updating document: ", e);
        }
    }

    async setOrUpdateDocumentById(collectionName, id, updateData) {
        try {
            // Referencia a la coleccin
            const collectionRef = collection(this.firestore, collectionName);
            
            // Consulta para encontrar el documento con la propiedad 'id' especificada
            const q = query(collectionRef, where('id', '==', id));
            const querySnapshot = await getDocs(q);
            
            if (querySnapshot.empty) {
                // No se encontr el documento, creamos uno nuevo
                const newDocRef = await addDoc(collectionRef, {
                    ...updateData,
                    id: id // Asegrate de agregar el 'id' en el nuevo documento
                });
                console.log("New document created with ID: ", newDocRef.id);
            } else {
                // Se encontr el documento, lo actualizamos
                const docSnapshot = querySnapshot.docs[0];
                const docId = docSnapshot.id;
                
                // Referencia al documento
                const docRef = doc(this.firestore, collectionName, docId);
                
                // Actualiza el documento
                await setDoc(docRef, updateData, { merge: true });
                console.log("Document updated with ID: ", docId);
            }
            
        } catch (e) {
            console.error("Error adding or updating document: ", e);
        }
    }

    async getFireStore(collectionName) {
        try {
            // Get all documents in the collection
            const collectionRef = collection(this.firestore, collectionName);
            const querySnapshot = await getDocs(collectionRef);
            const documents = [];
            querySnapshot.forEach((doc) => {
                documents.push({ id: doc.id, ...doc.data() });
            });
            return documents;
        } catch (e) {
            console.error("Error getting documents: ", e);
            return [];
        }
    }

    // Add a new document to Firestore
    async setFireStore(collectionName, json) {
        try {
            const docRef = await addDoc(collection(this.firestore, collectionName), json);
            console.log("Document written with ID: ", docRef.id);
        } catch (e) {
            console.error("Error adding document: ", e);
        }
    }

    // Get a document by ID
    async getDocument(collectionName, docId) {
        try {
            const docRef = doc(this.firestore, collectionName, docId);
            const docSnap = await getDoc(docRef);

            if (docSnap.exists()) {
                return docSnap.data();
            } else {
                console.log("No such document!");
                return null;
            }
        } catch (e) {
            console.error("Error getting document: ", e);
        }
    }

    // Upload files to Firebase Storage
    async uploadFiles(files, folderPath) {
        console.log(files);
        const uploadPromises = files.map(async file => {
            const fileRef = ref(this.storage, `${folderPath}/${file.file.name}`);
            await uploadBytes(fileRef, file.file);
            return getDownloadURL(fileRef);
        });
    
        try {
            const urls = await Promise.all(uploadPromises);
            console.log('Todos los archivos subidos con xito.');
            return urls; // Devuelve las URLs de los archivos
        } catch (error) {
            console.error('Error subiendo archivos: ', error);
            throw error; // Lanza el error para manejarlo en el lugar de llamada
        }
    }

    async uploadFilesProject(files) {
        const uploadPromises = files.map(async (file) => {
            const fileRef = ref(this.storage, `projects/${file.ruta}/${file.file.name}`);
            await uploadBytes(fileRef, file.file);
            // Obtener la URL de descarga del archivo
            const downloadURL = await getDownloadURL(fileRef);
            return downloadURL; // Retorna la URL del archivo
        });
    
        try {
            const urls = await Promise.all(uploadPromises);
            console.log('Todos los archivos subidos con xito.');
            return urls; // Devuelve las URLs de los archivos
        } catch (error) {
            console.error('Error subiendo archivos: ', error);
            throw error; // Lanza el error para manejarlo en el lugar de llamada
        }
    }
    
    

    async getDownloadURL(filePath) {
        try {
            const fileRef = ref(this.storage, filePath);
            const url = await getDownloadURL(fileRef);
            return url;
        } catch (error) {
            console.error('Error al obtener la URL de descarga', error);
            throw error;
        }
    }
    
    async updateRating(projectId, newRating) {
        try {
            // Obtener referencia al documento del proyecto
            const projectRef = doc(this.firestore, 'Proyectos', projectId);
            const projectDoc = await getDoc(projectRef);

            if (projectDoc.exists()) {
                const projectData = projectDoc.data();
                const previousVotes = projectData.votos || 0;
                const previousRating = projectData.valoracionPromedio || 0;

                // Calcular nueva valoracin promedio
                const newAverageRating = ((previousRating * previousVotes) + newRating) / (previousVotes + 1);
                const updatedVotes = previousVotes + 1;

                // Actualizar el documento con la nueva valoracin y votos
                await updateDoc(projectRef, {
                    valoracionPromedio: newAverageRating,
                    votos: updatedVotes
                });

                console.log(`Valoracin actualizada con xito: ${newAverageRating} en ${updatedVotes} votos.`);
            } else {
                console.error("El documento del proyecto no existe.");
            }
        } catch (e) {
            console.error("Error al actualizar la valoracin en Firestore: ", e);
        }
    }
    
}

export default FireBaseConexion;
