import { useState, useEffect, useCallback } from 'react';
import FireBaseConexion from '../Services/FireBase';

const fireBaseConexion = new FireBaseConexion();

export function usePageProyecto(id) {
    const [dataProyecto, setDataProyecto] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    const fetchProyecto = useCallback(async () => {
        try {
            setLoading(true); // Comienza la carga
            const proyecto = await fireBaseConexion.getDocument('Proyectos', id);
            setDataProyecto(proyecto);
        } catch (err) {
            console.error('Error fetching project:', err);
            setError(err);
        } finally {
            setLoading(false); // Finaliza la carga
        }
    }, [id]);

    useEffect(() => {
        if (id) {
            fetchProyecto();
        }
    }, [id, fetchProyecto]);

    return {
        dataProyecto,
        loading,
        error,
    };
}

export default usePageProyecto;
