import React from 'react';
import './Experiencia.css'; // Asegrate de crear este archivo CSS
import { Tooltip } from '@mui/material';
import { useExperiencia } from './useExperiencia';

function Experiencia() {
    const { experiencia } = useExperiencia();
    return (
        <div className="container-section" style={{ position: 'relative' }}>
            {/* <div style={{ position: 'absolute', right: '-430px', top: '-100px' }}>
                <a href="http://autosdebodas.com.ar/" target="_blank" rel="noopener noreferrer">
                    <Tooltip title="Visita Autos de Bodas">
                        <img src={`${process.env.PUBLIC_URL}/images/collage_AutosDeBodas.png`} alt="Collage" />
                    </Tooltip>
                </a>
            </div>
            <div style={{ position: 'absolute', left: '-500px', top: '280px' }}>
                <img style={{ width: '410px' }} src={`${process.env.PUBLIC_URL}/images/collage2.png`} alt="Collage" />
            </div> */}
            <h2>Experiencia</h2>
            <ol className="timeline">
                {experiencia.map((item, index) => (
                    <li key={index} className="timeline-item"> 
                        <div className="timeline-content">
                            <h3 className='highlight'>{item.empresa}</h3>
                            <p style={{ fontSize: '.875rem' }}>{`${item.fechaInicio}-${item.fechaFin}`}</p>
                            <p style={{ marginBottom: '10px' }}>{item.descripcion}</p>
                            <ul>
                                {item.Tareas.map((tarea, tareaIndex) => (
                                    <li key={tareaIndex}>
                                        <p>{tarea}</p>
                                    </li>
                                ))}
                            </ul>
                        </div>
                    </li>
                ))}
            </ol>
        </div>
    );
}

export default Experiencia;
