import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import usePageProyecto from './usePageProyecto';
import './PageProyecto.css';
import StarRating from '../Components/StarRating/StarRating';

function PageProyecto() {
    const { id } = useParams();
    const { dataProyecto, loading, error } = usePageProyecto(id);

    const [playingIndex, setPlayingIndex] = useState(null);

    // Manejo de estados de carga y error
    if (loading) {
        return <div>Cargando...</div>;
    }

    if (error) {
        return <div>Error al cargar el proyecto: {error.message}</div>;
    }

    // Si dataProyecto no est disponible
    if (!dataProyecto) {
        return <div>No se encontr el proyecto.</div>;
    }

    // Funcin para manejar el clic en el video
    const handleVideoClick = (index) => {
        const videoElement = document.getElementById(`video-${index}`);
        if (playingIndex === index) {
            videoElement.pause();
            setPlayingIndex(null);
        } else {
            videoElement.play();
            setPlayingIndex(index);
        }
    };

    return (
        <div className="page-proyecto">
            {dataProyecto.imgPresentacion && (
                <header className="container-title">
                    <div className="project-title d-flex">
                        <figure className="d-flex align-items-center">
                            <img
                                src={dataProyecto.imgPresentacion}
                                alt={dataProyecto.titulo}
                                className="project-image"
                            />
                        </figure>
                        <div className="d-flex flex-column justify-content-center">
                            <h2>{dataProyecto.titulo}</h2>
                            <StarRating currentRating={dataProyecto.valoracionPromedio} votos={dataProyecto.votos} projectId={id} />
                        </div>
                    </div>
                </header>
            )}

            <section className="project-description">
                {dataProyecto?.descripcionDetallada?.map((parrafo,index) => (
                    <p key={index}>{parrafo}</p>
                ))}
            </section>

            {/* Imgenes del proyecto */}
            <section className="project-images">
                {dataProyecto?.imagenes?.sort((a, b) => a.orden - b.orden)
                    .map((imagen,index) => {
                        if (imagen.imagen !== "") {
                            return (
                                <figure className="d-flex mb-4 align-items-center card-imagenes" key={index}>
                                    <img src={imagen.imagen} alt={imagen.tituloImagen} />
                                    <figcaption className="d-flex flex-column">
                                        <h3>{imagen.tituloImagen}</h3>
                                        <p>{imagen.descripcionImagen}</p>
                                    </figcaption>
                                </figure>
                            );
                        }
                    })}
            </section>

            {/* Renderizar los videos */}
            {dataProyecto?.videosPresentacion?.videos && dataProyecto.videosPresentacion.videos.length > 0 && (
                <section className="project-videos">
                    <h2 className='w-100 text-center underline'>{dataProyecto.videosPresentacion.descripcionVideos}</h2>
                    {dataProyecto.videosPresentacion.videos.map((video, index) => (
                        <>
                        <p>{video.tituloVideo}</p>
                        {video.video !== "" && (
                            <figure key={index} className='card-video'>
                                <div className="col-video">
                                    <video
                                        id={`video-${index}`}
                                        className="project-video"
                                        onClick={() => handleVideoClick(index)}
                                        muted
                                    >
                                        <source src={video.video} type="video/mp4" />
                                        Tu navegador no soporta el elemento de video.
                                    </video>
                                    {playingIndex !== index && (
                                        <div className="play-icon" onClick={() => handleVideoClick(index)}>
                                            <span></span>
                                        </div>
                                    )}
                                </div>
                            </figure>
                        )
                        }
                    </>
                    ))}
                </section>
            )}

        </div>
    );
}

export default PageProyecto;
