import React, { useState } from 'react';
import { useThemeContext } from '../ThemeContext/useThemeContext';
import FireBaseConexion from '../../Services/FireBase';

const fireBaseConexion = new FireBaseConexion();

// Funcin para redondear a medios valores
const roundToHalf = (num) => {
    return Math.round(num * 2) / 2;
};

function StarRating({ currentRating, votos, projectId, totalRatings }) {
    const { blackTheme } = useThemeContext();
    const [rating, setRating] = useState(roundToHalf(currentRating)); // Redondear al iniciar
    const [hovered, setHovered] = useState(0);
    const [votosLocal, setVotosLocal] = useState(votos);
    const [bandera, setBandera] = useState(true);
    const handleRating = async (starValue) => {
        try {
            if (bandera) {

                setBandera(false)
                setVotosLocal(votosLocal + 1)
                const roundedValue = roundToHalf(starValue);
                // Actualizar la valoracin en Firestore
                await fireBaseConexion.updateRating(projectId, roundedValue);
                // Actualizar el estado local con la nueva valoracin
                setRating(roundedValue);
            }
        } catch (error) {
            console.error("Error al actualizar la valoracin:", error);
        }
    };

    return (
        <div className="star-rating">
            {[...Array(5)].map((_, index) => {
                const starValue = index + 1;
                const isFilled = starValue <= (hovered || rating);

                return (
                    <span
                        key={starValue}
                        className={`star ${isFilled ? 'filled' : ''}`}
                        onMouseEnter={() => setHovered(starValue)}
                        onMouseLeave={() => setHovered(0)}
                        onClick={() => handleRating(starValue)}
                        role="button"
                        aria-label={`${starValue} star${starValue > 1 ? 's' : ''}`}
                        style={{
                            fontSize: '24px',
                            color: isFilled ? (blackTheme ? 'gold' : 'black') : 'grey',
                        }}
                    >
                        
                    </span>
                );
            })}
            <p className='mb-0 pb-0'>Valoracin promedio: {currentRating?.toFixed(0) ?? rating}</p>
            <p className='mb-0 pb-0'>Votos: {votosLocal}</p>
        </div>
    );
}

export default StarRating;
