import React from 'react';
import './Proyectos.css';
import useProyectos from './useProyectos';
import { useThemeContext } from '../../../Components/ThemeContext/useThemeContext';
function Proyectos(props) {
  const {
    dataProyectos,
    visibleCount,
    loadMoreProjects,
    loadLessProjects

  } = useProyectos();
  const { blackTheme } = useThemeContext();


  return (
    <div className='container-section'>
      <h2>Proyectos</h2>
      <div className="grid-container">
        {dataProyectos
          ?.sort((a, b) => a.orden - b.orden)
          .slice(0, visibleCount) // Muestra solo los proyectos visibles
          .map((proyecto, index) => (
            <a href={`/proyecto/${proyecto.id}`} key={index} className={`card ${blackTheme ? 'black-theme' : 'white-theme'}`}>
              <figure>
                <img src={proyecto.imgPresentacion} alt={`Proyecto ${index}`} />
              </figure>
              <figcaption>
                <div className='row-descripcion'>
                  <h1 className="highlight">{proyecto.titulo}</h1>
                  <p style={{ textAlign: 'left' }}>
                    {proyecto?.descripcionCorta}
                  </p>
                </div>
                  <ul className="technologies-list d-flex">
                    {proyecto.tecnologias.map((tecnologia, techIndex) => (
                      <li key={techIndex}>{tecnologia}</li>
                    ))}
                  </ul>
              </figcaption>
            </a>
          ))}
      </div>

      <div className="button-container">
        {visibleCount > 4 && (
          <button className="btn-ver-menos" onClick={loadLessProjects}>
            Ver menos
          </button>
        )}
        {visibleCount < dataProyectos.length && (
          <button className="btn-ver-todos" onClick={loadMoreProjects}>
            Ver ms
          </button>
        )}

      </div>
    </div>
  );
}

export default Proyectos;
