import React, { useEffect, useState } from 'react';
import FireBaseConexion from '../../../Services/FireBase';

export function useExperiencia() {
    const [experiencia, setExperiencia] = useState([]);
    const firebase = new FireBaseConexion();

    useEffect(() => {
        // Definir la funcin asncrona dentro del useEffect
        const fetchData = async () => {
            try {
                const carrera = await firebase.getFireStore('Carrera');
                setExperiencia(carrera[0].experienciaLaboral);
            } catch (error) {
                console.error('Error fetching data: ', error);
            }
        };

        fetchData();  // Llamar la funcin asncrona
    }, []);

    return {
        experiencia
    };
}
