import React from 'react';
import './SobreMi.css'; // Asegrate de crear este archivo CSS
import DownloadButton from '../../../Components/DownloadButton/DownloadButton'
import ArticleIcon from '@mui/icons-material/Article';
function SobreMi() {
  return (
    <div className='container-section'>

      <h2>Sobre M</h2>
      <div className='avatar-container'>
        <div className='avatar'>
          <div className="card-inner">
            <div className="card-front">
              <img src={`${process.env.PUBLIC_URL}/images/avatarPrimerPlano.png`} alt="Avatar" className="avatar-front" />
            </div>
            <div className="card-back">
              <img src={`${process.env.PUBLIC_URL}/images/LogoSommie.png`} alt="Sommie" className="avatar-back" />
            </div>
          </div>
        </div>
      </div>
      <p>
        Me llamo Joaqun Burgos. Me encanta programar y disfruto resolver problemas y aprender constantemente.
        En mi tiempo libre, suelo desarrollar proyectos que buscan tener un impacto real, como el bot que dise para optimizar la obtencin de entradas en eventos,
        ayudando a reducir el tiempo de espera en filas virtuales.
      </p>
      <p>
        Junto con un amigo, creamos Sommie, una entidad imaginaria donde llevamos a cabo proyectos como excusa para aprender nuevas tecnologas.
        Cada idea que surge la aprovechamos como una oportunidad para experimentar y desarrollar nuestras habilidades en herramientas y lenguajes que nos interesa dominar.
        De esta manera, no solo innovamos, sino que tambin nos mantenemos al da con lo ltimo en el mundo tecnolgico.
      </p>
      <p>
        Me motiva la idea de que el cdigo que escribo pueda tener un uso prctico, simplificando tareas o resolviendo problemas,
        y siempre estoy buscando maneras de crecer como desarrollador a travs de proyectos que aporten valor.
      </p>
      <div className='d-flex justify-content-center mt-4'>
      <DownloadButton
        text='Curriculum'
        fileUrl={`${process.env.PUBLIC_URL}/documents/CV JOAQUIN BURGOS.docx.pdf`}
        SvgIcon={ArticleIcon}
      />

      </div>
    </div>
  );
}

export default SobreMi;
