// ThemeContext.js
import React, { createContext, useContext, useState } from 'react';

const ThemeContext = createContext();

export const useThemeContext = () => {
  return useContext(ThemeContext);
};

export const ThemeProvider = ({ children }) => {
  const [blackTheme, setBlackTheme] = useState(true);
  const [currentPage, setCurrentPage] = useState('portfolio'); // Estado para controlar la pgina actual
  const [idProyecto, setIdProyecto] = useState(null);

  const cambiarTheme = () => {
    setBlackTheme(prev => !prev);
  };

  const goToPortfolio = () => {
    setCurrentPage('portfolio'); 
    window.history.pushState({}, '', '/'); 
    setIdProyecto(null);
  };

  const goToProyecto = (id) => {
    setCurrentPage('proyecto'); // Cambia a la vista del proyecto
    window.history.pushState({}, '', `/proyecto/${id}`); 
    setIdProyecto(id);
  };

  return (
    <ThemeContext.Provider value={{ blackTheme, cambiarTheme, currentPage, goToPortfolio, goToProyecto }}>
      {children}
    </ThemeContext.Provider>
  );
};
