import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App'; 
import { ThemeProvider } from './Components/ThemeContext/useThemeContext';
import Header from './Header/Header';
import { useThemeContext } from './Components/ThemeContext/useThemeContext';

const RootComponent = () => {
  const { blackTheme } = useThemeContext();

  return (
    <main className={`main ${blackTheme ? 'black-theme' : 'white-theme'}`}>
      <Header />
      <App />
    </main>
  );
};

const root = ReactDOM.createRoot(document.getElementById('root'));

root.render(
  <ThemeProvider>
    <RootComponent />
  </ThemeProvider>
);
