import React, { useEffect, useRef } from 'react';
import './Tecnologias.css'; // Asegrate de que este archivo CSS est bien enlazado
import useTecnologias from './useTecnologias';

function Tecnologias() {
    const { tecnologias } = useTecnologias();
    const cardRefs = useRef([]);

    // Agrupar tecnologas por tipo
    const groupedTecnologias = tecnologias.reduce((acc, tecnologia) => {
        const tipo = tecnologia.Tipo || 'Otros'; // Agrupamos por tipo o 'Otros'
        if (!acc[tipo]) {
            acc[tipo] = [];
        }
        acc[tipo].push(tecnologia);
        return acc;
    }, {});

    // Mostrar las tarjetas
    const displayedCards = Object.keys(groupedTecnologias).map((tipo, index) => (
        <div
            className='container noselect'
            ref={el => (cardRefs.current[index] = el)} // Asignar la referencia
            key={index}
        >
            <div class="canvas">
                <div class="tracker tr-1"></div>
                <div class="tracker tr-2"></div>
                <div class="tracker tr-3"></div>
                <div class="tracker tr-4"></div>
                <div class="tracker tr-5"></div>
                <div class="tracker tr-6"></div>
                <div class="tracker tr-7"></div>
                <div class="tracker tr-8"></div>
                <div class="tracker tr-9"></div>
                <div class="tracker tr-10"></div>
                <div class="tracker tr-11"></div>
                <div class="tracker tr-12"></div>
                <div class="tracker tr-13"></div>
                <div class="tracker tr-14"></div>
                <div class="tracker tr-15"></div>
                <div class="tracker tr-16"></div>
                <div class="tracker tr-17"></div>
                <div class="tracker tr-18"></div>
                <div class="tracker tr-19"></div>
                <div class="tracker tr-20"></div>
                <div class="tracker tr-21"></div>
                <div class="tracker tr-22"></div>
                <div class="tracker tr-23"></div>
                <div class="tracker tr-24"></div>
                <div class="tracker tr-25"></div>
                <div id="card">
                    <h3>{tipo}</h3>
                    <div className="tecnologia-images"> {/* Contenedor para las imgenes */}
                        {groupedTecnologias[tipo].map((tecnologia) => (
                            <div key={tecnologia.id}>
                                <div style={{ height: '60px' }}>
                                    <img src={tecnologia.urlImg[0]} alt={tecnologia.nombreTecnologia} className='tecnologia-image' />
                                </div>
                                <h4 className="tecnologia-name">{tecnologia.nombreTecnologia}</h4> {/* Aadido nombre aqu */}
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    ));

    return (
        <div className='container-section'>
            <h2>Tecnologas</h2>
            <div className='tecnologias-container'>
                {displayedCards}
            </div>
        </div>
    );
}

export default Tecnologias;
