import React from 'react';
import MyButton from '../../../Components/MyButton';
import { LinkedIn, GitHub, MailOutline } from '@mui/icons-material'; // Importar los conos desde MUI
import './Presentacion.css';

function Presentacion() {
  const startDate = new Date('2022-08-15'); // Fecha de inicio
  const currentDate = new Date(); // Fecha actual
  const yearsOfExperience = currentDate.getFullYear() - startDate.getFullYear(); // Calcula la diferencia en aos

  // Ajustar si el ao actual es anterior al mes y da de inicio
  const hasNotAnniversaryYet =
    currentDate.getMonth() < startDate.getMonth() ||
    (currentDate.getMonth() === startDate.getMonth() && currentDate.getDate() < startDate.getDate());

  const experience = hasNotAnniversaryYet ? yearsOfExperience - 1 : yearsOfExperience;

  return (
    <>
      <h1 className="welcome-message">
        Bienvenido a mi Portfolio!
      </h1>
      <p>
        Soy Joaco, un <strong className='highlight'>Desarrollador de Software Full Stack</strong> con ms de {experience} aos de experiencia.<br />
      </p>


      <div className="button-container">
        <MyButton
          text="LinkedIn"
          SvgIcon={LinkedIn} // Icono de LinkedIn
        />
        <MyButton
          text="Github"
          SvgIcon={GitHub} // Icono de Github
        />
        <MyButton
          text="Contctame"
          SvgIcon={MailOutline} // Icono de contacto
        />
      </div>
    </>
  );
}

export default Presentacion;
