import React from 'react';
import PropTypes from 'prop-types';
import './DownloadButton.css'; // Asegrate de tener estilos en este archivo

function DownloadButton({ text, fileUrl,SvgIcon }) {
    return (
        <a href={fileUrl} download style={{ textDecoration: 'none' }}>
            <button className="button">
                <svg fill="currentColor" viewBox="0 0 24 24" className="icon">
                    {SvgIcon && <SvgIcon style={{ marginRight: '8px', width: '24px', height: '24px' }} />}
                </svg>
                {text}
            </button>
        </a>
    );
}

DownloadButton.propTypes = {
    text: PropTypes.string.isRequired,
    fileUrl: PropTypes.string.isRequired, // Prop para la URL del archivo a descargar
};

export default DownloadButton;
