import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import PagePortfolio from './PagePortfolio/PagePortfolio';  
import PageProyecto from './PageProyecto/PageProyecto';    

function App() {

  return (
    <Router>
      <Routes>
        <Route path="/" element={<PagePortfolio />} />
        <Route path="/proyecto/:id" element={<PageProyecto />} />
      </Routes>
    </Router>
  );
}

export default App;
