import { useState, useEffect, useCallback } from 'react';
import FireBaseConexion from '../../../Services/FireBase'; 

const fireBaseConexion = new FireBaseConexion();

export function useTecnologias() {
    const [tecnologias, setTecnologias] = useState([]);
    
    const fetchDocuments = useCallback(async (collectionName) => {
        try {
            const tecnologias = await fireBaseConexion.getFireStore(collectionName);
            setTecnologias(tecnologias);
            console.log(tecnologias);
        } catch (error) {
            console.error('Error fetching documents:', error);
        }
    }, []);

    useEffect(() => {
        fetchDocuments('Tecnologias');
    }, [fetchDocuments]);

    return {
        tecnologias,
    };
}

export default useTecnologias;
