import React from 'react';
import PropTypes from 'prop-types';
import './MyButton.css'; // Importa los estilos CSS

function MyButton({ SvgIcon, text, onClick, styles,download }) {
  return (
    <button
      style={styles}
      onClick={onClick} className="boton-elegante">
      {SvgIcon && <SvgIcon style={{ marginRight: '8px', width: '24px', height: '24px' }} />}
      <span>{text}</span>
    </button>
  );
}

MyButton.propTypes = {
  SvgIcon: PropTypes.elementType,
  text: PropTypes.string.isRequired,
  onClick: PropTypes.func.isRequired,
};

export default MyButton;
