import React, { useState, useEffect } from 'react';
import '../styles.css';
import Header from '../Header/Header';
import Presentacion from './Sections/Presentacion/Presentacion';
import Experiencia from './Sections/Experiencia/Experiencia';
import Proyectos from './Sections/Proyectos/Proyectos';
import SobreMi from './Sections/SobreMi/SobreMi';
import Tecnologias from './Sections/Tecnologias/Tecnologias';
import { useThemeContext } from '../Components/ThemeContext/useThemeContext';

function PagePortfolio() {
    const { blackTheme } = useThemeContext();
    const [cargando, setCargando] = useState(true);

    // Simulacin de carga
    useEffect(() => {
        const timer = setTimeout(() => {
            setCargando(false);
        }, 2000);

        return () => clearTimeout(timer); 
    }, []);

    return (
        <>
            {cargando ? (
                <div style={{height:'100vh',width:'100vw', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                    Cargando...
                </div>
            ) : (
                <div>
                    <section id='presentacion' className="section">
                        <Presentacion />
                    </section>
                    <section id='experiencia' className="section">
                        <Experiencia />
                    </section>
                    <section id='proyectos' className="section">
                        <Proyectos  />
                    </section>
                    <section id='sobre-mi' className="section">
                        <SobreMi />
                    </section>
                    <section id='tecnologias' className="section">
                        <Tecnologias />
                    </section>
                </div>
            )}
        </>
    );
}

export default PagePortfolio;
